.techs_main {
    width: 80%;
    margin: auto;
    margin-top: 2rem;
    animation: fadeIn 1s;
    .category_section {
        margin-bottom: 3rem;
        .category_title {
            font-family: lobster;
            font-size: 2rem;
            position: relative;
            &::after {
                content: '';
                width: 100%;
                height: 2px;
                border-bottom: 1px dashed #fff;
                position: absolute;
                left: 0;
                bottom: -10px;
            }
        }
        .category_items {
            margin-top: 2rem;
            display: flex;
            flex-wrap: wrap;
            .item {
                font-family: 'lato';
                margin: 5px 0;
                margin-right: 1rem;
                background-color: #333;
                padding: 0.5rem 0.8rem;
                border-radius: 50vh;
                text-align: center;
            }
        }
    }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
    .techs_main {
        width: 90%;
        margin-top: 1rem;
        animation: fadeIn 1s;
        .category_section {
            margin-bottom: 2rem;
            .category_title {
                font-size: 1.4rem;
            }
            .category_items {
                margin-top: 1rem;
                .item {
                    margin: 5px 0;
                    margin-right: 1rem;
                    font-size: 0.9rem;
                }
            }
        }
    }
}
